
'use server';

type ApiResponse = {
    status: boolean;
    response: {
        username: string;
        email: string;
        avatar: string;
        country: string;
        role: {
            name: string;
        };
        steam_id: string;
        total_km: number;
        total_jobs: number;
        longest_job: number;
        id: number;
        registered: string;
    };
};

export type UserProfile = {
    username: string;
    email: string;
    avatar: string;
    country: string;
    role: {
        name: string;
    };
    steam_id: string;
    total_km: number;
    total_jobs: number;
    longest_job: number;
    id: number;
    registered: string;
    license_number: string;
    division: string | null;
    timezone: string;
};

export async function getProfileData(): Promise<UserProfile | null> {
    const apiKey = process.env.TRUCKERSHUB_API_KEY;
    if (!apiKey) {
        console.error("TRUCKERSHUB_API_KEY is not set.");
        return null;
    }

    try {
        const url = `https://api.truckershub.in/v1/user`;
        const res = await fetch(url, {
            headers: { Authorization: apiKey },
            next: { revalidate: 300 } // Revalidate every 5 minutes
        });
        
        if (!res.ok) {
            console.error(`Failed to fetch user data:`, res.status, await res.text());
            return null;
        }

        const data: ApiResponse = await res.json();
        
        if (data && data.status && data.response) {
            const userResponse = data.response;
            return {
                ...userResponse,
                license_number: `NL${userResponse.id}${new Date(userResponse.registered).getTime() % 100000}`,
                division: 'Alpha',
                timezone: 'Asia/Kolkata', // Placeholder
            };
        } else {
            console.error(`Invalid API response structure for user:`, data);
            return null;
        }
    } catch (error) {
        console.error(`Error fetching user data:`, error);
        return null;
    }
}
